@import "lib/_default.scss";
@import "lib/_form.scss";
main {
	h1 {
		font-weight: 300;
	}
	.addMore {
		text-align: right;
		margin-bottom: 2em;
		a {
			@mixin button-accent 1,
			true;
			display: inline-block;
		}
	}
	.search {
		margin-bottom: 2em;
	}
	ul {
		list-style: none;
		li {
			@mixin card 1,
			true;
			padding: 1.5em;
			border-radius: 0;
			lost-utility: clearfix;
			&:hover {
				transform: scale(1.01);
			}
			&:first-child {
				border-top-left-radius: 4px;
				border-top-right-radius: 4px;
			}
			&:last-child {
				border-bottom-left-radius: 4px;
				border-bottom-right-radius: 4px;
			}
			.title {
				lost-column: 4/6;
				font-size: 1.2em;
				.secondary {
					font-size: .9em;
					color: $text--secondary;
				}
			}
			.actions {
				lost-column: 2/6;
				text-align: right;
				a {
					@mixin button-default 1,
					false,
					0;
					margin-right: 1em;
				}
				button {
					@mixin button-danger 1,
					false,
					0;
				}
			}
		}
	}
	p {
		@mixin card;
		text-align: center;
		color: $text--secondary;
	}
}
